import Api from '@/api/index';

class TypeOfSport extends Api {

  /**
   * Вернет список всех видов спорта
   * @returns {Promise<Response>}
   */
  TypeOfSport = () => this.rest('/typeofsport/list.json');

  /**
   * Удалит вид спорта по id
   * @param id
   * @returns {Promise<*>}
   */
  remove = ( id ) => this.rest('/typeofsport/delete-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify({ id }),
  }).then(() => id) // then - заглушка, пока метод ничего не возвращает

  /**
   * Создаст новую запись в таблице
   * @param TypeOfSport объект вида спорта, взятый из TypeOfSportGroup
   * @returns {Promise<Response>}
   */
  add = ( TypeOfSport ) => this.rest('/typeofsport/add-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify(TypeOfSport),
  }).then(() => ({...TypeOfSport, id: new Date().getTime()})) // then - заглушка, пока метод ничего не возвращает

  /**
   * Отправит измененную запись
   * @param TypeOfSport объект вида спорта, взятый из TypeOfSportGroup
   * @returns {Promise<*>}
   */
  update = ( TypeOfSport ) => this.rest('/typeofsport/update-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify(TypeOfSport),
  }).then(() => TypeOfSport) // then - заглушка, пока метод ничего не возвращает

}

export default new TypeOfSport();
