import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'SportClub',
    component: () => import('@/views/ListTables')
  },
  {
    path: '/sportsmans/:type_id?',
    name: 'Sportsmans',
    props: (route) => {
      return {
        type_id: route.params.type_id,
      }
    },
    component: () => import('@/views/SportsmansPage')
  },
  {
    path: '/typeofsport',
    name: 'TypeOfSport',
    component: () => import('@/views/TypeOfSportPage'),
  },
  {
    path: '/sportsmans-edit/:id?',
    name: 'SportsmansEdit',
    props: (route) => {
      return {
        id: route.params.id,
      }
    },
    component: () => import('@/views/SportsmansEdit'),
  },
  {
    path: '/typeofsport-edit/:id?',
    name: 'TypeOfSportEdit',
    props: (route) => {
      return {
        id: route.params.id,
      }
    },
    component: () => import('@/views/TypeOfSportEdit'),
  },
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: () => import('@/views/SportsmansPage'),
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
})

export default router
