import Api from '@/api/index';

class Sportsmans extends Api {

  /**
   * Вернет список всех Спортсменов
   * @returns {Promise<Response>}
   */
  Sportsmans = () => this.rest('/sportsmans/list.json');

  /**
   * Удалит Спортсмена по id
   * @param id
   * @returns {Promise<*>}
   */
  remove = ( id ) => this.rest('/sportsmans/delete-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify({ id }),
  }).then(() => id) // then - заглушка, пока метод ничего не возвращает

  /**
   * Создаст новую запись в таблице
   * @param Sportsman объект спортсмена, взятый из FormSportsmans
   * @returns {Promise<Response>}
   */
  add = ( Sportsman ) => this.rest('/sportsmans/add-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify(Sportsman),
  }).then(() => ({...Sportsman, id: new Date().getTime()})) // then - заглушка, пока метод ничего не возвращает

  /**
   * Отправит измененную запись
   * @param Sportsman объект спортсмена, взятый из FormSportsmans
   * @returns {Promise<*>}
   */
  update = ( Sportsman ) => this.rest('/sportsmans/update-item', {
    method: 'POST',
    'Content-Type': 'application/json',
    body: JSON.stringify(Sportsman),
  }).then(() => Sportsman) // then - заглушка, пока метод ничего не возвращает

}

export default new Sportsmans();
