import api from './api';

export default {
  namespaced: true,
  state: {
    items: [],
  },
  getters: {
    items: state => state.items,
    itemsByKey: state => state.items.reduce((res, cur) => {
      res[cur['id']] = cur;
      return res;
    }, {}),
    itemsByType: state => state.items.reduce((res, cur) => {
      const id = parseInt(cur['TYPE_OF_SPORT_ID']);
      res[id] = (id in res) ? [...res[id], cur] : [cur];
      return res;
    }, {}),
  },
  mutations: {
    setItems: (state, items) => {
      state.items = items;
    },
    setItem: (state, item) => {
      state.items.push(item);
    },
    removeItem: (state, idRemove) => {
      state.items = state.items.filter(({ id }) => id !== idRemove)
    },
    updateItem: (state, updateItem) => {
      const index = state.items.findIndex(item => +item.id === +updateItem.id);
      state.items[index] = updateItem;
    }
  },
  actions: {
    fetchItems: async ({ commit }) => {
      const response = await api.Sportsmans();
      const items = await response.json();
      commit('setItems', items)
    },
    removeItem: async ({ commit }, id) => {
      const idRemovedItem = await api.remove( id );
      commit('removeItem', idRemovedItem);

    },
    addItem: async ({ commit }, { Name, Photo, TPE_OF_SPORT_ID, Biography, Year }) => {
      const item = await api.add({Name, Photo, TPE_OF_SPORT_ID, Biography, Year })
      commit('setItem', item)
    },
    updateItem: async ({ commit }, { id, Name, Photo, TPE_OF_SPORT_ID, Biography, Year }) => {
      const item = await api.update({ id, Name, Photo, TPE_OF_SPORT_ID, Biography, Year});
      commit('updateItem', item);
    }
  },
}
